<template>
  <div>
    <div v-if="!loading">
      <div class="me-header">
        <div class="me-header-inner">
          <div class="me-name">您好,<span class="me-namew">{{me.name}}</span></div>
          <div class="me-count">余额：{{me.balance}}</div>
        </div>
      </div>
      <!-- <mt-cell title="全部礼品" is-link to="/items"></mt-cell> -->
      <mt-cell title="我的订单" is-link to="/MyOrder"></mt-cell>
      <mt-cell title="我的收货地址" is-link to="/#/address"></mt-cell>
      <mt-cell title="积分兑换奖品规则" is-link></mt-cell>
      <div class="items-footer">
        <div class="items-footer-btn" @click="buy">商品列表</div>
        <div class="items-footer-btn" @click="myInfo">我的信息</div>
      </div>
    </div>
    <div class="loading">
      <mt-spinner v-if="loading" color="#26a2ff" :size="80" type="fading-circle" class="loading2"></mt-spinner>
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    me () {
      return this.$store.state.me.me
    },
    loading () {
      if (this.$store.state.me.me.name) {
        return false
      } else {
        return true
      }
    }
  },
  beforeRouteEnter (to, from, next) {
    next(vm => {
      vm.$store.dispatch('getMe')
    })
  },
  methods: {
    buy () {
      this.$router.push('/items')
    },
    myInfo () {
      this.$router.push('/me')
    }
  }
}
</script>

<style scoped>
.items-footer {
  position: fixed;
  bottom: 0;
  right: 0;
  left: 0;
  height: 45px;
  line-height: 45px;
  display: flex;
  font-size: 18px;
  text-align: center;
}
.items-footer-btn {
  flex: 1;
  border: 1px solid #ececec;
  background: #26a2ff;
  color: #fff;
}
.me-header {
  height: 200px;
  background: #0894ec;
  display: flex;
  text-align: center;
  font-family: monospace;
}
.me-header-inner {
  margin: auto;
  font-size: 20px;
  background: #fff;
  border-radius: 200px;
}
.me-count, .me-name {
  padding: 0 20px;
  margin: 15px 0;
  background: white;
  border-radius: 50px;
  text-align: left;
}
.me-namew {
  color: #e96900;
}
.loading {
  text-align: center;
  margin-top: 100px;
}
.loading2 {
  display: inline-block;
}
</style>



