import api from '../../api/api'
import {responseHandle} from '../../utils/utils'

export default {
  state: {
    me: {}
  },
  mutations: {
    listMe (state, payload) {
      state.me = payload
    }
  },
  actions: {
    getMe ({commit, state}, payload) {
      let user = localStorage.user
      user = user ? JSON.parse(user) : undefined
      if (user && user._time > Date.now()) {
        commit('listMe', user)
      } else {
        api.getMe(payload).then(({body}) => {
          responseHandle(body, () => {
            body._time = Date.now() + 60000 // 一分钟缓存
            localStorage.user = JSON.stringify(body)
            commit('listMe', body)
          })
        })
      }
    }
  }
}



