'use strict'

// 对于restful中参数形如：id的参数放在params.url中，其他的放在params.body

import Vue from 'vue'
import VueResource from 'vue-resource'
import qs from 'query-string'

Vue.use(VueResource)

const host = location.host.match('localhost') || location.host.match('8080') ? '/api' : '/shop'

const methods = ['post', 'get', 'delete', 'put']

// 需要使用四个http动词的api接口
let common = {
  order: '/order',
  items: '/items',
  users: '/users',
  address: '/address',
  me: '/users/me'
}

// 生成方法接口和http动词的对象；
function generateHttpAction (apis, methods) {
  var tmpApi = {}
  for (var api in apis) {
    for (var methodIndex in methods) {
      var apiName = methods[methodIndex] + api[0].toUpperCase() + api.slice(1)
      tmpApi[apiName] = {
        url: apis[api],
        method: methods[methodIndex]
      }
    }
  }
  return tmpApi
}

var commonTransform = generateHttpAction(common, methods)

let extra = {
  userLogin: {
    url: '/users/jumpLogin/lecture/18825053886/oiTCLt_SZ5jBF5tKG7OoXMC3AMZM',
    method: 'get'
  },
  putOrderStatus: {
    url: '/order/status',
    method: 'put',
    rest: true
  },
  addressOn: {
    url: '/address/on',
    method: 'put'
  }
}

let apis = {}
Object.assign(apis, commonTransform, extra)

let webInterface = {}
for (var api in apis) {
  let { url, method, rest } = apis[api]
  webInterface[api] = (params) => {
    if (rest) {
      // restful中url中会带有参数
      if (method === 'delete') {
        return Vue.http[method](host + url + '/' + params.url, params)
      } else {
        return Vue.http[method](host + url + '/' + params.url, params.body)
      }
    } else {
      if (method === 'delete') {
        return Vue.http[method](host + url, {body: params})
      } else if (method !== 'get') {
        return Vue.http[method](host + url, params)
      } else {
        return Vue.http[method](host + url + '?' + qs.stringify(params))
      }
    }
  }
}

export default webInterface



