<template>
  <div>
    <mt-search
      v-model="keyword"
      cancel-text="取消"
      placeholder="搜索"
      :show=true>
      <div class="goods" v-for="i in list" :key="i.id" @click="gotoDetail(i.id)">
        <div class="goods-img" :style="{backgroundImage: 'url(' + i.img + ')'}">
        </div>
        <div class="goods-info">
          <div class="goods-title">{{i.name}}</div>
          <div class="goods-desc">{{i.description}}</div>
        </div>
      </div>
    </mt-search>
    <div class="items-footer">
      <div class="items-footer-btn" @click="buy">商品列表</div>
      <div class="items-footer-btn" @click="myInfo">我的信息</div>
    </div>
    <div>
    </div>
  </div>
</template>

<script>
import {mapGetters} from 'vuex'
export default {
  computed: {
    ...mapGetters({
      list: 'listOfIntro'
    }),
    keyword: {
      get () {
        return this.$store.state.items.keyword
      },
      set (value) {
        this.$store.dispatch('getItems', {
          keyword: value
        })
      }
    }
  },
  beforeRouteEnter (to, from, next) {
    next(vm => {
      vm.$store.dispatch('getItems')
    })
  },
  methods: {
    gotoDetail (id) {
      this.$router.push({
        path: '/ItemDetail',
        query: {
          id: id
        }
      })
    },
    buy () {
      this.$router.push('/items')
    },
    myInfo () {
      this.$router.push('/me')
    }
  }
}
</script>

<style>
.mint-searchbar {
  background: #26a2ff;
}
.mint-searchbar-cancel {
  color: #fff;
}
.goods-img {
  width: 110px;
  height: 110px;
  background-size: cover;
  background-position: center;
}
.goods {
  margin: 5px 0;
  padding: 15px 5px;
  position: relative;
  font-size: 14px;
  font-family: sans-serif, cursive, fantasy;
  border-bottom: 1px #ccc dashed;
}
.goods-info {
  position: absolute;
  left: 120px;
  top: 0;
  right: 0;
  bottom: 0;
}
.goods-title {
  margin: 5px 20px 0 0;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
}
.goods-desc {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
  margin: 10px 20px 0 0;
}
.items-footer {
  position: fixed;
  bottom: 0;
  right: 0;
  left: 0;
  height: 45px;
  line-height: 45px;
  display: flex;
  font-size: 18px;
  text-align: center;
}
.items-footer-btn {
  flex: 1;
  border: 1px solid #ececec;
  background: #26a2ff;
  color: #fff;
}
</style>



