import api from '../..//api/api'
import {responseHandle, removeFromList} from '../../utils/utils'
import router from '../../router/index'

export default {
  state: {
    page: undefined,
    keyword: undefined,
    list: []
  },
  mutations: {
    listOrder (state, payload) {
      state.list = payload.list
      if (payload.page) {
        state.page = payload.page
      }
      if (payload.keyword) {
        state.keyword = payload.keyword
      }
    }
  },
  actions: {
    getOrder ({commit, state}, payload) {
      api.getOrder(payload)
      .then(({body}) => {
        responseHandle(body, () => {
          commit('listOrder', {list: body, ...payload})
        })
      })
    },
    postOrder ({commit, state}, payload) {
      let {list} = state
      api.postOrder(payload)
      .then(({body}) => {
        responseHandle(body, () => {
          payload.id = body.id
          list.push(payload)
          commit('listOrder', {list: list})
          router.push({
            path: '/paysuccess',
            query: payload
          })
        })
      })
    },
    putOrder ({commit, state}, payload) {
      let {list} = state
      api.putOrder(payload.body)
      .then(({body}) => {
        responseHandle(body, () => {
          let newList = list.map((item) => {
            if (item.id === payload.body[0]) {
              item.status = payload.url
            }
            return item
          })
          commit('listOrder', {list: newList})
        })
      })
    },
    cancelOrder ({commit, state}, payload) {
      let {list} = state
      api.cancelOrder(payload)
      .then(({body}) => {
        responseHandle(body, () => {
          let newList = list.map((item) => {
            if (item.id === payload.id) {
              item.status = 0
            }
            return item
          })
          commit('listOrder', {list: newList})
        })
      })
    },
    deleteOrder ({commit, state}, payload) {
      let {list} = state
      api.deleteOrder(payload)
      .then(({body}) => {
        responseHandle(body, () => {
          commit('listOrder', {list: removeFromList(list, payload[0])})
        })
      })
    },
    putOrderStatus ({commit, state}, payload) {
      let {list} = state
      api.putOrderStatus({
        url: 3,
        body: [payload]
      })
      .then(({body}) => {
        responseHandle(body, () => {
          let newList = list.map((item) => {
            if (item.id === payload) {
              item.status = 2
            }
            return item
          })
          commit('listOrder', {list: newList})
        })
      })
    }
  }
}



