const initPicker = (picker, val, idx) => {
  setTimeout(function () {
    picker.setSlotValue(idx, val)
  }, 50)
}
const getUrlParam = function (name) {
  var reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)') // 构造一个含有目标参数的正则表达式对象
  var r = window.location.hash.substr(1).match(reg) // 匹配目标参数
  if (r != null) return decodeURI(r[2]); return null // 返回参数值
}
export default {
  data () {
    var q = this.$route.query
    var status = q.status
    if (typeof status === 'string') {
      if (status === 'false') {
        status = false
      } else {
        status = true
      }
    }
    return {
      id: q.id,
      val: [q.province, q.city],
      username: q.name,
      phone: q.phone,
      district: q.district,
      details: q.details,
      status: status
    }
  },
  computed: {
    slots: () => {
      let pro = getUrlParam('province')
      pro = pro === undefined ? '广东' : pro
      return [
        {
          flex: 1,
          values: ['四川', '重庆', '广东', '陕西', '甘肃', '青海', '宁夏', '云南',
            '澳门', '贵州', '香港', '辽宁', '吉林', '黑龙江', '海南', '广西',
            '湖北', '湖南', '河南', '台湾', '北京', '河北', '天津', '内蒙古',
            '山西', '浙江', '江苏', '上海', '山东', '江西', '福建', '安徽', '西藏', '新疆'],
          textAlign: 'right'
        }, {
          divider: true,
          content: '-'
        }, {
          flex: 1,
          values: city[pro],
          textAlign: 'left'
        }
      ]
    }
  },
  methods: {
    onValuesChange (picker, values) {
      this.val = values[0] && values[1] ? values : this.val
      if (values[0] === undefined) {
        initPicker(picker, this.val[0] ? this.val[0] : '广东', 0)
      } else if (values[1] === undefined) {
        initPicker(picker, this.val[1] ? this.val[1] : '广州', 1)
      } else {
        picker.setSlotValues(1, city[values[0]])
      }
    },
    postAddress () {
      let {username, phone, val: [province, city], district, details, status} = this
      if (username === undefined || username === '') {
        this.$store.dispatch('toast', {msg: '请填写收货人姓名'})
      } else if (!(/^1[0-9]{10}$/.test(phone))) {
        this.$store.dispatch('toast', {msg: '请填写正确的手机号码'})
      } else if (district === undefined || district === '') {
        this.$store.dispatch('toast', {msg: '请填写区镇'})
      } else if (details === undefined || details === '') {
        this.$store.dispatch('toast', {msg: '请填写街道地址'})
      } else {
        var address = {
          name: username,
          phone: phone,
          province: province,
          city: city,
          district: district,
          details: details,
          status: status
        }
        if (this.id && this.id > 0) {
          address.id = this.id
          this.$store.dispatch('putAddress', address)
        } else {
          this.$store.dispatch('postAddress', address)
        }
      }
    }
  }
}
</script>

<style>
.city .mint-cell-title {
  width: 200px;
}
.saveBtn {
  margin: 20px 0;
}
</style>



