<template>
  <div>
    <div class="addressAdd" v-if="!address">
      <mt-button size="large" @click="addAddress">设置收货地址</mt-button>
    </div>
    <div class="address" v-if="address" @click="addressList">
      <div class="top">
        <div class="user-name">收货人：{{address.name}}</div>
        <div class="phone">{{address.phone}}</div>
      </div>
      <div class="middle">
        收货地址：{{allAddress}}
      </div>
      <i class="mint-cell-arrow-right"></i>
    </div>
    <div class="goods">
      <div class="goods-img" :style="{backgroundImage: 'url(' + goods.img + ')'}">
      </div>
      <div class="goods-info">
        <div class="goods-n">{{goods.name}}</div>
        <div class="goods-price">价格: ¥{{goods.price}}</div>
      </div>
    </div>
    <mt-button class="submit-order" type="primary" size="large" @click="postOrder">提交订单</mt-button>
  </div>
</template>

<script>
export default {
  data () {
    return {}
  },
  computed: {
    address () {
      let address = this.$store.state.address.list.filter((item) => {
        return item.status
      })[0]
      return address
    },
    allAddress () {
      let {province, city, district, details} = (this.address === undefined ? {} : this.address)
      return province + city + district + details
    },
    goods () {
      return this.$route.query
    }
  },
  beforeRouteEnter (to, from, next) {
    next(vm => {
      vm.$store.dispatch('getAddress')
    })
  },
  methods: {
    addAddress () {
      if (this.$store.state.address.list.length > 0) {
        this.$router.push('/address')
      } else {
        this.$router.push('/addAddress')
      }
    },
    addressList () {
      this.$router.push('/address')
    },
    postOrder () {
      let {address, goods, allAddress} = this
      if (address === undefined) {
        this.$store.dispatch('toast', {msg: '请先完善地址信息'})
      } else {
        this.$store.dispatch('postOrder', {
          item_id: goods.item_id,
          name: goods.name,
          price: goods.price,
          img: goods.img,
          user_name: address.name,
          phone: address.phone,
          address: allAddress
        })
      }
    }
  }
}
</script>

<style scoped>
.address {
  cursor: pointer;
  padding: 10px 15px;
  font-size: 14px;
  border-bottom: 1px solid #ececec;
  color: #fff;
  background: #26a2ff;
  margin-bottom: 15px;
}
.addressAdd {
  margin: 0 0 20px;
}
.top {
  display: flex;
  justify-content: space-between;
  font-size: 18px;
  margin: 5px 0;
}
.goods {
  margin: 5px 0;
  padding: 0 0 15px;
  position: relative;
  font-size: 14px;
  font-family: sans-serif, cursive, fantasy;
  border-bottom: 1px #ccc dashed;
}
.goods-img {
  width: 110px;
  height: 110px;
  background-size: cover;
  background-position: center;
}
.goods-info {
  position: absolute;
  left: 120px;
  top: 0;
  right: 0;
  bottom: 0;
}
.goods-price {
  position: absolute;
  bottom: 15px;
}
.submit-order {
  position: fixed;
  bottom: 0;
  height: 50px;
  line-height: 50px;
  font-size: 20px;
  color: white;
}
</style>



