import api from '../../api/api'
import {responseHandle} from '../../utils/utils'

export default {
  state: {
    page: undefined,
    keyword: undefined,
    list: []
  },
  mutations: {
    listItems (state, payload) {
      state.list = payload.list
      if (payload.keyword) {
        state.keyword = payload.keyword
      }
      if (payload.page) {
        state.page = payload.page
      }
    }
  },
  actions: {
    getItems ({commit, state}, payload) {
      api.getItems(payload).then(({body}) => {
        localStorage.items = JSON.stringify(body)
        responseHandle(body, () => {
          commit('listItems', {list: body, ...payload})
        })
      })
    }
  },
  getters: {
    listOfIntro (state, getters) {
      return state.list.map((item) => {
        item.img = JSON.parse(item.specifications)[0].img[0]
        return item
      })
    }
  }
}



