<template>
  <div class="pay-success">
    <div class="pay-success-header">
      <div class="pay-header-inner">
        <div class="pay-header-inner-top">已成功兑换礼品</div>
        <div class="pay-header-inner-bottom">您的礼品正整装待发</div>
      </div>
    </div>
    <div class="pay-success-body">
      <div class="pay-info-card">
        <mt-cell title="商品名称" :label="order.name"></mt-cell>
        <mt-cell title="地址" :label="order.address"></mt-cell>
        <mt-cell title="收货人" :value="order.user_name"></mt-cell>
        <mt-cell title="手机号" :value="order.phone"></mt-cell>
      </div>
    </div>
    <div class="pay-footer">
      <div class="pay-footer-btn" @click="seeOrder">查看订单</div>
      <div class="pay-footer-btn" @click="index">返回首页</div>
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    order () {
      return this.$route.query
    }
  },
  methods: {
    index () {
      this.$router.push('/items')
    },
    seeOrder () {
      this.$router.push('/MyOrder')
    }
  }
}
</script>

<style>
.mint-cell-wrapper, .mint-cell:last-child {
  background: none;
}
.pay-success {
  background: #ececec;
  height: 100%;
}
.pay-success-header {
  position: relative;
  height: 100px;
  background: #26a2ff;
}
.pay-header-inner {
  position: absolute;
  left: 40px;
  top: 20px;
  color: #fff;
  font-weight: bold;
}
.pay-header-inner-top {
  font-size: 20px;
}
.pay-info-card {
  background: white;
  margin: 8px;
  border-radius: 8px;
  padding: 5px;
}
.pay-name {
  font-size: 20px;
}
.pay-footer {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  height: 50px;
  line-height: 50px;
  display: flex;
}
.pay-footer-btn {
  flex: 1;
  border: 1px solid #ececec;
  text-align: center;
  font-size: 20px;
}
</style>



