<template>
  <div>
    <div class="order-wrap" v-for="item in orders" :key="item.id">
      <div class="order">
        <div class="order-img" :style="{backgroundImage: 'url(' + item.img + ')'}">

        </div>
        <div class="order-right">
          <div class="order-name">
            {{item.name}}
          </div>
          <div class="order-price">
            ¥{{item.price}}
          </div>
        </div>
      </div>
      <div class="op">
        <mt-button v-if="item.status==0" size="small" disabled>已取消</mt-button>
        <mt-button v-if="item.status==1" size="small" disabled>待发货</mt-button>
        <mt-button v-if="item.status==2" size="small" type="primary" @click="confirm(item.id)">确认收货</mt-button>
        <mt-button v-if="item.status==3" size="small" disabled>已完成</mt-button>
      </div>
    </div>
    <div class="no-orders" v-if="orders.length==0">
      还没有订单哦~
      <div style="marginTop:20px;">
        <mt-button size="small" @click="back">返回</mt-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    orders () {
      let orders = this.$store.state.order.list
      return orders
    }
  },
  beforeRouteEnter (to, from, next) {
    document.title = '我的订单'
    next(vm => {
      vm.$store.dispatch('getOrder')
    })
  },
  methods: {
    confirm (id) {
      this.$store.dispatch('putOrderStatus', id)
    },
    back () {
      history.back()
    }
  }
}
</script>

<style scoped>
.no-orders {
  color: #888;
  margin: 40px auto;
  text-align: center;
}
.order-wrap {
  margin: 10px 0;
  padding: 5px 10px;
  border-bottom: 1px solid #ececec;
}
.order {
  position: relative;
}
.order-img {
  width: 100px;
  height: 100px;
  margin: 3px;
}
.order-right {
  position: absolute;
  left: 110px;
  top: 0;
  bottom: 0;
  right: 0;
}
.op {
  text-align: right;
}
.order-img {
  background-size: cover;
  background-position: center;
}
</style>



