import { Toast } from 'mint-ui'

export function responseHandle (body, cb) {
  if (body.errCode) {
    Toast(body.msg)
  } else {
    cb(body)
  }
}

export function addToList (list, item) {
  let {length} = list
  if (length) {
    if (Object.keys(list[length - 1]).length) {
      list.push(item)
    } else {
      list.pop()
      list.push(item)
    }
  } else {
    list.push(item)
  }
  return list
}

export function removeFromList (list, id) {
  return list.filter((item) => {
    return item.id !== id
  })
}



