import api from '../../api/api'
import {responseHandle} from '../../utils/utils'
import { MessageBox, Toast, Indicator } from 'mint-ui'

const handleRst = ({body, status, id}) => {
  responseHandle(body, () => {
    if (status) {
      api.addressOn({'id': id}).then(({body}) => {
        responseHandle(body, () => {
          history.back()
        })
      })
    } else {
      history.back()
    }
  })
}

export default {
  state: {
    list: [],
    loading: true
  },
  mutations: {
    listAddress (state, payload) {
      state.loading = payload.loading
      state.list = payload.list
    }
  },
  actions: {
    getAddress ({commit, state}) {
      api.getAddress()
      .then(({body}) => {
        responseHandle(body, () => {
          commit('listAddress', {list: body})
        })
      })
    },
    postAddress ({commit, state}, payload) {
      Indicator.open()
      api.postAddress(payload).then(({body}) => {
        handleRst({body, status: payload.status, id: body.id})
        Indicator.close()
      })
    },
    putAddress ({commit, state}, payload) {
      Indicator.open()
      api.putAddress(payload).then(({body}) => {
        handleRst({body, status: payload.status, id: payload.id})
        Indicator.close()
      })
    },
    deleteAddress ({commit, state}, payload) {
      MessageBox.confirm('确定删除该地址?').then(action => {
        let {list} = state
        api.deleteAddress(payload).then(({body}) => {
          responseHandle(body, () => {
            let newList = list.filter((item) => {
              return item.id !== payload.ids[0]
            })
            commit('listAddress', {list: newList})
          })
        })
      })
    },
    addressOn ({commit, state}, payload) {
      let {list} = state
      commit('listAddress', {list: list, loading: true})

      api.addressOn(payload).then(({body}) => {
        responseHandle(body, () => {
          let newList = list.map((item) => {
            if (item.status) {
              item.status = 0
            }
            if (item.id === payload.id) {
              item.status = 1
            }
            return item
          })
          Toast(body.msg)
          commit('listAddress', {list: newList, loading: false})
        })
      })
    }
  }
}



