<template>
  <div>
    <div v-if="!loading" class="container">
      <div class="address-item" v-for="i in list" :key="i.id">
        <div class="top">
          <div class="name">{{i.name}}</div>
          <div class="phone">{{i.phone}}</div>
        </div>
        <div class="middle">{{i.province + i.city + i.district + i.details}}</div>
        <div class="bottom">
          <mt-badge type="warning" size="large" v-if="i.status">默认地址</mt-badge>
            <mt-button v-if="!i.status" type="primary" size="small" @click="addressOn(i)">设为默认</mt-button>
          <div class="op-panel">
            <mt-button size="small" type="primary" @click="editAddress(i)">编辑</mt-button>
            <mt-button size="small" plain @click="deleteAddress(i.id)">删除</mt-button>
          </div>
        </div>
      </div>
      <div class="no-address" v-if="list.length==0">
        还没有收货地址哦~
      </div>
      <div class="addressAdd">
        <mt-button type="primary" size="large" @click="addAddress">添加新地址</mt-button>
      </div>
    </div>
    <div v-if="loading" class="loading">
      <mt-spinner color="#26a2ff" :size="80" type="fading-circle" class="loading2"></mt-spinner>
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    list () {
      return this.$store.state.address.list
    },
    loading () {
      return this.$store.state.address.loading
    }
  },
  beforeRouteEnter (to, from, next) {
    document.title = '管理收货地址'
    next(vm => {
      vm.$store.dispatch('getAddress')
    })
  },
  methods: {
    deleteAddress (id) {
      this.$store.dispatch('deleteAddress', {
        ids: [id]
      })
    },
    addressOn (i) {
      if (!i.status) {
        this.$store.dispatch('addressOn', {
          id: i.id
        })
      }
    },
    editAddress (i) {
      this.$router.push({
        path: '/addAddress',
        query: i
      })
    },
    addAddress () {
      this.$router.push('/addAddress')
    }
  }
}
</script>

<style scoped>
.address-item {
  margin-bottom: 10px;
  padding: 20px 14px;
  border-bottom: 1px #ccc dashed;
}
.no-address {
  text-align: center;
  color: #888;
  margin: 20px 0;
}
.top {
  display: flex;
  justify-content: space-between;
  font-size: 20px;
}
.middle {
  margin: 5px 0;
}
.bottom {
  display: -webkit-box;
  justify-content: space-between;
}
.addressAdd {
  text-align: center;
  padding: 5px 20px;
}
.loading {
  text-align: center;
  margin-top: 100px;
}
.loading2 {
  display: inline-block;
}
</style>



